import React from 'react';
import { withRouter } from 'react-router-dom';
import styles from './Compose.module.css';

function Compose({ history, id, blogs, setBlogs }) {
	const submitHandler = event => {
		event.preventDefault();

		if (
			event.target[0].value.trim() !== '' &&
			event.target[1].value.trim() !== '' &&
			event.target[2].value.trim() !== ''
		) {
			const newBlog = {
				id: id + 1,
				title: event.target[0].value,
				description: event.target[1].value,
				content: event.target[2].value,
				date: getDate(),
				len: getLen(event.target[2].value.trim())
			};

			const updatedBlogs = [...blogs];
			updatedBlogs.unshift(newBlog);
			setBlogs(updatedBlogs);
			localStorage.setItem('data', JSON.stringify(updatedBlogs));

			const confirm = window.confirm('Post this Blog?');
			if (confirm) {
				history.push('/');
			}
		} else {
			window.alert('Please fill before Submitting!');
		}
	};

	const getDate = () => {
		const date = new Date();
		const monthNames = [
			'January',
			'February',
			'March',
			'April',
			'May',
			'June',
			'July',
			'August',
			'September',
			'October',
			'November',
			'December'
		];

		const day = date.getDate();
		const monthIndex = date.getMonth();
		const year = date.getFullYear();

		return day + ' ' + monthNames[monthIndex] + ' ' + year;
	};

	const getLen = content => {
		const words = content.split(' ');
		const len = parseInt(words.length / 25);
		const cups = len / 5 + 1;
		return `${len} min read ${'☕'.repeat(cups)}`;
	};

	return (
		<div className={styles.Compose}>
			<form onSubmit={submitHandler}>
				<h1>Compose a new Blog</h1>
				<input type="text" placeholder="Title of the Blog" />
				<input type="text" placeholder="Description" />
				<textarea placeholder="Content (Use Markdown)"></textarea>
				<div className={styles.buttons}>
					<button type="submit">Post</button>
					<button type="reset">Clear</button>
				</div>
			</form>
		</div>
	);
}

export default withRouter(Compose);
