import React from 'react';
import { Link } from 'react-router-dom';

import styles from './Home.module.css';

function Home({ blogs }) {
	return (
		<div className={styles.Home}>
			{blogs.map(blog => (
				<BlogContainer
					key={blog.id}
					id={blog.id}
					title={blog.title}
					description={blog.description}
					date={blog.date}
					len={blog.len}
				/>
			))}
		</div>
	);
}

function BlogContainer({ title, id, description, date, len }) {
	return (
		<div className={styles.BlogContainer}>
			<h1>
				<Link to={`/blog/${id}`}>{title}</Link>
			</h1>
			<p>
				{date} &middot; {len}
			</p>
			<p>{description}</p>
		</div>
	);
}

export default Home;
