import React, { useState, useEffect } from 'react';
import { Route, Switch } from 'react-router-dom';
import { data } from './data';

import Home from './components/Home/Home';
import Blog from './components/Blog/Blog';
import Navbar from './components/Navbar/Navbar';
import Compose from './components/Compose/Compose';

function App() {
	const [blogs, setBlogs] = useState(data);

	useEffect(() => {
		if (localStorage.getItem('data')) {
			setBlogs(JSON.parse(localStorage.getItem('data')));
		} else {
			localStorage.setItem('data', JSON.stringify(data));
		}
	}, []);

	return (
		<div className="App">
			<Route path="/" component={Navbar} />
			<Switch>
				<Route path="/" exact render={() => <Home blogs={blogs} />} />
				<Route
					path="/compose"
					render={() => (
						<Compose id={blogs.length} blogs={blogs} setBlogs={setBlogs} />
					)}
				/>
				<Route path="/blog/:id" exact render={() => <Blog blogs={blogs} />} />
				<Route path="/" component={Four0Four} />
			</Switch>
		</div>
	);
}

function Four0Four() {
	return (
		<div className="four-0-four">
			<h1>404</h1>
			<small>We are not sure what you are looking for!</small>	
		</div>
	);
}

export default App;
