import React from 'react';
import { withRouter } from 'react-router-dom';
import { Remarkable } from 'remarkable';

import styles from './Blog.module.css';

function Blog({ match, history, blogs }) {
	const index = blogs.findIndex(
		blog => parseInt(match.params.id) === parseInt(blog.id)
	);

	if (blogs[index] === undefined) {
		history.push('/error');
	}

	const getRawMarkup = () => {
		const md = new Remarkable();
		return { __html: md.render(blogs[index].content) };
	};

	return (
		<div className={styles.Blog}>
			<h1>{blogs[index].title}</h1>
			<div className={styles.info}>
				{blogs[index].date} &middot; {blogs[index].len}
			</div>
			<div
				className={styles.content}
				dangerouslySetInnerHTML={getRawMarkup()}
			></div>
			<footer>William Shakespeare</footer>
		</div>
	);
}

export default withRouter(Blog);
